.AboutProduct {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  backdrop-filter: blur(1.5px);

  .container {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 40px;
    width: 1097px;
    height: 750px;
    font-size: 40px;
    font-weight: 700;
    background: #FFFFFF;
    box-shadow: 0px 4px 60px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    box-sizing: border-box;
    transform: translate(-50%, -50%);
    
    .header {
      padding-left: 150px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .closeIcon {
        width: 40px;
        height: 40px;
        padding-left: 1px;
        padding-bottom: 11px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 44px;
        font-weight: 100;
        border-radius: 50%;
        background-color: gainsboro;
        transform: rotate(45deg);
        box-sizing: border-box;
        transition: background-color 0.3s;
        cursor: pointer;

        &:hover {
          background: rgb(168, 167, 167);
        }
      }
    }

    .text {
      margin-top: 40px;
      font-weight: 400;
      font-size: 24px;

      .list {
        list-style: none;

        li {
          position: relative;
  
          &::before {
            content: '';
            position: absolute;
            right: calc(100% + 20px);
            top: 50%;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background: teal;
            transform: translateY(calc(-50% + 4px));
          }
        }
      }

      ul {
        list-style-type: decimal;
      }

      .example {
        position: relative;

        &::before {
          content: "";
          position: absolute;
          z-index: -1;
          top: -115px;
          left: 400px;
          width: 589px;
          height: 288px;
          background: url('../../assets/images/example1.png');
          transform: scale(0.6);
        }

        &::after {
          content: '';
          position: absolute;
          top: 15px;
          left: 200px;
          width: 260px;
          height: 58px;
          background: url('../../assets/images/example.png');
        }
      }
    }

    .title {
      font-size: 40px;
      font-weight: 700;
      cursor: pointer;
      transition: color 0.3s;

      &:hover {
        color: teal;
      }
    }

    .activeTab {
      position: relative;
      color: teal;

      &::after {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 2px;
        background: teal;
        top: calc(100% + 20px);
      }
    }
  }
}